.filter--div {
  display: inline-block;
  margin: 10px;
  border: 1px solid #eee;
  box-shadow: 0 2px 2px #ccc;
  width: 200px;
  padding: 20px;
  align-content: center;
}

.Product--name {
  font-size: 15px;
  text-transform: uppercase;
  margin-top: 20px;
  text-align: center;
}
.Product--cost {
  font-size: 15px;
  text-transform: uppercase;
  text-align: center;
}
.Product--desc {
  font-size: 15px;
  text-transform: uppercase;
  text-align: center;
}
.filter--div__img {
  width: 100%;
  height: 204.75px;
}
